.contenedorCompForm {
  display: flex;
  justify-content: center;
}

.compraMasFormulario {
  height: 450px;
  width: 81%;
  display: flex;
  justify-content: center;
}

.contenedorCompras {
  width: 45%;
  background-color: #2A2A2A;
  overflow: auto;
  position: relative;
}

.totalFinal {
  font-size: 35px;
  color: #F2F2F2;
  text-align: center;
  position: absolute;
  bottom: 0;
}

.productoComprado {
  margin: 30px;
  display: flex;
  height: 80px;
  background-color: #F2F2F2;
}

.imgCompra {
  width: 30%;
  background-color: #82BD4B;
  display: flex;
  justify-content: center;
  align-items: center;
}

.imgCompra img {
  width: 80%;
  height: 80%;
  object-fit: contain;
}

.infoProductoComprado {
  margin: auto;
}

.carNombre,
.precioTotal,
.cantidadPrecio {
  font-family: 'Outfit', sans-serif;
  color: #2A2A2A;
  margin: 0;
  padding: 0;
}

.eliminarProducto {
  width: 20%;
  border: none;
  cursor: pointer;
  background-color: transparent;
  transition: transform 1s ease;
}

.eliminarProducto:hover {
  transform: scale(1.1);
}

.contenedorFromulario {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 55%;
  background-color: #F2F2F2;
}

.confirmacion {
  font-size: 1.43rem;
  color: 2A2A2A;
  margin: 0 0 30px;
  padding: 0;
}

.formulario {
  width: 80%;
}

.formulario label {
  margin-bottom: 10px;
  color: #2A2A2A;
}

.formulario input {
  width: 99%;
  border: none;
  border-bottom: 2px solid #2A2A2A;
  background-color: transparent;
  margin-bottom: 30px;
}

.botonFormulario {
  display: flex;
  justify-content: flex-start;
}

.enviar {
  background-color: #82BD4B;
  color: #2A2A2A;
  border: none;
  font-weight: 700;
  padding: 10px 20px;
  cursor: pointer;
  transition: transform 1s ease;
}

.enviar:hover {
  transform: scale(1.1);
}

.carritoVacio {
  font-family: 'Outfit', sans-serif;
  text-align: center;
  font-size: 2rem;
  color: #F2F2F2;
}

.verProductos {
  text-align: center;
  font-size: 1.2rem;
  margin: 0;
}

.compraExitosa {
  margin: 12% auto;
  padding: 5%;
  background-color: #82BD4B;
  color: #F2F2F2;
  font-size: 2rem;
}

.totalFinalMovil {
  display: none;
}

@media (max-width: 415px) {
  .compraMasFormulario {
    margin: 74px 0 40px;
    flex-direction: column;
    height: auto;
    width: 100%;
  }

  .contenedorCompras {
    width: 100%;
  }

  .productoComprado {
    margin: 20px;
    height: 65px;
  }

  .carNombre,
  .precioTotal,
  .cantidadPrecio {
    font-size: 16px;
  }

  .contenedorFromulario {
    width: 100%;
    background-color: #F2F2F2;
    margin-bottom: 10px;
  }

  .formulario {
    width: 90%;
  }

  .confirmacion {
    width: 90%;
    justify-content: center;
  }

  .totalFinalMovil {
    display: block;
    font-size: 24px;
    color: #F2F2F2;
    text-align: center;
    position: relative;
    margin: 10px;
  }

  .totalFinal {
    display: none;
  }

  .botonFormulario {
    display: flex;
    justify-content: center;
  }

  .carritoVacioVerProductos {
    margin-top: 50%;
  }

  .carritoVacio {
    margin: 0;
  }

}